import React, { useState } from 'react';

import Match from '../components/Match';
import Axios from 'axios';

function UserBets({ user }) {
    const [limit, setLimit] = useState([0, 10]);

    const [currentMatch, setCurrentMatch] = useState([]);
    // console.log(currentMatch.length);
    if (currentMatch.length === 0) {
        let keyBets = Object.keys(user.bets);
        const orderBets = []
        keyBets.forEach((id) => {
            orderBets[id] = {
                date: user.bets[id].date || 0,
                idMatch: id
            }
        });
        orderBets.sort((a, b) => {
            return b.date - a.date;
        })
        orderBets.map(({ date, idMatch }) => {
            const currentBet = user.bets[idMatch];
            // console.log(idMatch, currentBet);
            Axios.get(`../api/match/${idMatch}`).then(({ data }) => {
                currentMatch.push(<Match newMatch={data} key={idMatch} bets={user.bets} />)
            })


        })
        // console.log(currentMatch)
        setTimeout(() => {

            setCurrentMatch(currentMatch);
        }, 1000);

        return (
            <div>

            </div>
        )
    } else {
        return (
            <div style={{
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center',
                alignItems: 'center',
            }}>
                <div className="nav" style={{
                    width: '100%',
                    height: '40px',
                    marginBottom: '20px',
                    display: 'inline-flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    background: '#cc682d',
                    color: '#fff',
                }}>
                    <div
                        style={{
                            background: '#FFF',
                            // borderRadius: '50%',
                            textAlign: 'center',
                            height: '40px',
                            width: '40px',
                            fontSize: '30px',
                            lineHeight: '34px',
                            color: '#000',
                            cursor: 'pointer',
                            border: '3px solid #FFF',
                            margin: '5px',
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF40';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#FFF';
                        }} onMouseLeave={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#000';
                        }}
                        onClick={() => {
                            if (limit[0] - 10 >= 0) {
                                setLimit([limit[0] - 10, limit[1] - 10])
                            }
                        }}
                    >
                        {"<"}
                    </div>
                    <div
                        style={{
                            background: '#FFF',
                            // borderRadius: '50%',
                            textAlign: 'center',
                            height: '40px',
                            width: '40px',
                            fontSize: '30px',
                            lineHeight: '34px',
                            color: '#000',
                            cursor: 'pointer',
                            border: '3px solid #FFF',
                            margin: '5px',
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF40';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#FFF';
                        }} onMouseLeave={(e) => {
                            e.currentTarget.style['background'] = '#FFFFFF';
                            e.currentTarget.style['border'] = '3px solid #FFF';
                            e.currentTarget.style['color'] = '#000';
                        }}
                        onClick={() => {
                            if (limit[0] + 10 <= Object.keys(user.bets).length) {
                                setLimit([limit[0] + 10, limit[1] + 10])
                            }
                        }}
                    >
                        {">"}
                    </div>
                </div>
                {currentMatch.slice(limit[0], limit[1])}
            </div>
        )
    }



}

export default UserBets;