import React, { useState, useRef } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Switch from "react-switch";
import Modal from '../../components/Modal';
import Achievement from '../../components/Achievement';
import UserBets from '../../components/UserBets';

const axios = require('axios');



function EditProfil({ history }) {






    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false) {
        setModal({
            show: !modal.show,
            message: (clean) ? modal.message : ""
        })
    }

    function sendProfil() {
        setModal({
            show: true,
            message: ''
        });
        axios.post('../api/updatePlayer', {
            user: user,
        })
            .then(function (response) {
                setModal({
                    show: true,
                    message: response.data.message
                });
                if (response.data.redirect) {

                    setTimeout(() => {
                        cookie.remove('login');
                        cookie.remove('user');
                        cookie.remove('bets');
                        history.push('/')

                    }, 3000)
                }
                setTimeout(() => {
                    setModal({
                        show: false,
                        message: ''
                    });
                }, 4500)
            })
            .catch(function (error) {
                console.log(error);
            });

    }
    const [userName, setUserName] = useState(cookie.load('user').login);
    const [user, setUser] = useState(null);
    const [listTeams, setListTeams] = useState(null);
    const loveTeam = useRef(null)

    if (user != null) {
        if (listTeams === null) {
            axios.get('/api/allTeams').then((response) => {
                // console.log(response.data)
                setListTeams(response.data.map((team) => {
                    let [name, ext] = team.split('.')
                    // console.log(name,ext,team)

                    return (
                        <div key={team} style={{
                            height: '50px',
                            width: '50px',
                            background: (user.loveTeam === name) ? `#ffffff center/70% url("./imgs/teams/${team}") no-repeat`: `center/70% url("./imgs/teams/${team}") no-repeat`,
                            border: (user.loveTeam === name) ? '2px solid #cc682d' : '2px solid #ffffff00',
                            cursor: 'pointer',
                        }} onMouseEnter={({ currentTarget }) => {
                            currentTarget.style['transform'] = 'scale(1.5)';
                        }} onMouseLeave={({ currentTarget }) => {
                            currentTarget.style['transform'] = 'scale(1)';
                        }} onClick={()=>{
                            axios.post('../api/loveTeam', {
                                teamId: name,
                                user: userName
                            }).then(()=>{
                                loveTeam.current.querySelector('.list').style['display'] = 'none';
                                loveTeam.current.querySelector('.icon').style['background'] = ` center/70% url("./imgs/teams/${team}") no-repeat`;
                                loveTeam.current.querySelector('.icon').style['border'] = '2px solid #cc682d';
                                user.loveTeam = name
                                setUser(user);
                                setListTeams(null);
                                                        })
                        }} />
                    )
                }))


            })
        }
        return (
            <div className="Profil">
                <Modal modal={modal} toggleModal={toggleModal}></Modal>
                <h2>Mon Profil</h2>
                <div className="user" style={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'center',
                    alignItems: 'center',
                }}>

                    <div className="name" style={{
                        margin: '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div style={{
                            width: '200px',
                        }}>Pseudo :</div>
                        <input type="text" id="pseudo" style={{
                            border: 'none',
                            borderBottom: '2px solid #cc682d'
                        }} defaultValue={user.name} onBlur={(event) => {
                            user.name = (event.target.value) ? event.target.value : user.name;
                            cookie.save('user', user, { path: '/' });
                            setUser(user);
                        }} />
                        <div className="alert" style={{
                            transform: 'scaleX(0)',
                        }}>
                            Ton pseudo n'est pas ton login
                            </div>

                    </div>
                    <div className="password" style={{
                        margin: '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div style={{
                            width: '200px',
                        }}>Password :</div>
                        <input type="password" id="password" placeholder="*********" onBlur={(event) => {
                            user.mdp = (event.target.value) ? event.target.value : user.mdp;
                            cookie.save('user', user, { path: '/' });
                            setUser(user);
                        }} />
                        <div className="alert" style={{
                            transform: 'scaleX(0)',
                        }}>
                            Si tu changes ton password, tu seras deconnect
                                </div>


                    </div>
                    <div className="email" style={{
                        margin: '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div htmlFor="email">Email :</div>
                        <input type="text" id="email" defaultValue={user.mail} onBlur={(event) => {
                            user.mail = (event.target.value) ? event.target.value : user.mail;
                            cookie.save('user', user, { path: '/' });
                            setUser(user);
                        }} />
                        <div className="alert" style={{
                            transform: 'scaleX(0)',
                        }}>
                            Soit averti de l'avance du projet
                                </div>

                    </div>
                    <div className="btag" style={{
                        margin: '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div htmlFor="btag">Btag :</div>
                        <input type="text" id="btag" defaultValue={user.btag} onBlur={(event) => {
                            user.btag = (event.target.value) ? event.target.value : user.btag;
                            cookie.save('user', user, { path: '/' });
                            setUser(user);
                        }} />
                        <div className="alert" style={{
                            transform: 'scaleX(0)',
                        }}>
                            Sans btag pas de recompenses possible
                                </div>

                    </div>
                    <div className="visibility" style={{
                        margin: '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        Profil Public : (A venir)<br />
                        <Switch
                            onChange={() => {
                                user.visibility = (user.visibility) ? !user.visibility : true;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }}
                            checked={(user.visibility) ? user.visibility : false}
                            onColor={'#cc682d'}
                            activeBoxShadow={'0 0 2px 3px #cc682d'}
                        ></Switch>

                    </div>
                    {/* <div>IMG (A venir)</div> */}
                    <div className="loveTeam" ref={loveTeam} style={{
                        margin: '0 auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div className="title">
                            LoveTeam :
                        </div>
                        <div className="icon" style={{
                            height: '50px',
                            width: '50px',
                            background: ` center/70% url("./imgs/teams/${user.loveTeam}.png") no-repeat`,
                            border: '2px solid #cc682d',
                            cursor: 'pointer',
                        }} onClick={() => {
                            loveTeam.current.querySelector('.list').style['display'] = 'flex';
                        }} />
                        <div className="list" onMouseLeave={() => {
                            loveTeam.current.querySelector('.list').style['display'] = 'none';
                        }} style={{
                            position: 'fixed',
                            flexWrap: 'wrap',
                            justifyContent: 'center',
                            alignItems: 'center',
                            width: '300px',
                            background: '#ffffff',
                            boxShadow: '0 3px 15px #cc682d',
                            marginTop: '-50px',
                            display: 'none',
                        }}>

                            {listTeams}
                        </div>
                    </div>
                    <div>

                    </div>
                    <div className="submit" style={{
                        margin: '30px auto',
                        maxWidth: '300px',
                        minWidth: '240px',

                    }}>
                        <div style={{
                            background: '#cc682d',
                            color: '#ffffff',
                            textAlign: 'center',
                            padding: '10px',
                            cursor: 'pointer',
                            fontSize: '1.3em',
                            border: '1px solid #cc682d',

                        }} onClick={sendProfil}
                            onMouseEnter={({ currentTarget }) => {
                                currentTarget.style['background'] = '#ffffff';
                                currentTarget.style['color'] = '#cc682d';
                                currentTarget.style['boxShadow'] = '0 3px 15px #cc682d';

                            }}
                            onMouseLeave={({ currentTarget }) => {
                                currentTarget.style['background'] = '#cc682d';
                                currentTarget.style['color'] = '#ffffff';
                                currentTarget.style['boxShadow'] = 'none';

                            }}>
                            Valider
                        </div>
                    </div>

                </div>
                <h2>Haut Fait</h2>
                <Achievement user={user} />
                <h2>Pronostic</h2>
                {(user.bets) ? <UserBets user={user} /> : ''}
            </div>
        )
    }
    else if (userName != null) {
        axios.get(`../api/getPlayer/${userName}`)
            .then(function (response) {
                setUser(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (<div></div>)
    }
    else {
        return (<div></div>)
    }






}



export default withRouter(EditProfil);