import React, { useState } from 'react';

const axios = require('axios');

function Achievement(props) {
    const [user] = useState(props.user);
    let Badges = [];

    let data = {
        valid: (user.inscript / 1000 < 1554076800) ? true : false,
        icon: "01.png",
        title: "First",
        name: "beta-close",
        hide: true,
        messag: "Attribu au joueur inscrit avant le 01-3-2019 (pendant la Bta Ferm)"

    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.inscript / 1000 < 1562544000) ? true : false,
        icon: "01.png",
        title: "Open Beta",
        hide: true,
        name: "beta-open",
        messag: "Attribu au joueur inscrit avant le 08-7-2019 (pendant la Bta Ouvert)"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    if (user.achievement && user.achievement.konami) {
        data = {
            valid: true,
            icon: "konami.png",
            title: "OldSchool",
            name: "konami",
            hide: true,
            messag: "U | U | D | D | L | R | L | R | B | A"

        }
        Badges.push(<Badge data={data} user={user} key={data.name} />)
    }



    data = {
        valid: (user.btag) ? true : false,
        icon: "03.png",
        title: "Loot'O'Vor",
        hide: true,
        name: "btag",
        messag: "Miam"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.btag && user.mail) ? true : false,
        icon: "02.png",
        title: "ValidUser",
        name: "validUser",
        hide: true,
        messag: "Attribu au joueur ayant rempli leurs profil"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.loveTeam) ? true : false,
        icon: "05.png",
        title: "Mon Equipe de Coeur",
        name: "loveTeam",
        hide: true,
        messag: "Attribu au joueur ayant choisie une Equipe de Coeur"
    }
    Badges.push(<Badge data={data} user={user} key={data.name} />)

    data = {
        valid: (user.bets) ? true : false,
        state: 0,
        icon: "bet.png",
        title: "Pronostiqueur en herbe",
        name: "bets-0",
        hide: false,
        messag: "Pronostiqu 1 match"
    }
    if (user.bets && user.score.totalBets >= 1) {
        data.state += 1;
        data.messag = user.score.totalBets + "/5"
        data.title = "Sorti de l'Iceberg";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 5) {
        data.state += 1;
        data.messag = user.score.totalBets + "/10"
        data.title = "Paresseux";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 10) {
        data.state += 1;
        data.messag = user.score.totalBets + "/15"
        data.title = "Initi";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 15) {
        data.state += 1;
        data.messag = user.score.totalBets + "/25"
        data.title = "Pile AA";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 25) {
        data.state += 1;
        data.messag = user.score.totalBets + "/50"
        data.title = "Un million de frappes";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 50) {
        data.state += 1;
        data.messag = user.score.totalBets + "/100"
        data.title = "Dlit d'acharnement";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 100) {
        data.state += 1;
        data.messag = user.score.totalBets + "/150"
        data.title = "A rythme de 7H par jour";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 150) {
        data.state += 1;
        data.messag = user.score.totalBets + "/250"
        data.title = "A rythme de 12H par jour";
        data.name = "bets-" + data.state;
    }
    // TOO HIGH
    if (user.bets && user.score.totalBets >= 250) {
        data.state = 8;
        data.messag = user.score.totalBets + "/500"
        data.title = "A rythme de 20H par jour";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 500) {
        data.state = 8;
        data.messag = user.score.totalBets + "/1000"
        data.title = "?? ?? ??";
        data.name = "bets-" + data.state;
    }
    if (user.bets && user.score.totalBets >= 1000) {
        data.state = 8;
        data.messag = user.score.totalBets + "/"
        data.title = "Error 404";
        data.name = "bets-" + data.state;
    }

    Badges.push(<Badge data={data} user={user} key={data.name} />)


    let keyBets = Object.keys(user.bets)
    let countLinkBetsValid = 0;
    let countLinkBetsFalse = 0;
    const result = {
        win: 0,
        winTeam: 0,
        lose: 0,
        loseTeam: 0,
        full: 0,
        fullTeam: 0,
    }
    keyBets.map((key) => {
        if (user.bets[key].valid) {
            const winTeam = (user.bets[key].resultA > user.bets[key].resultB) ? "A" : "B";
            const winBet = (user.bets[key].scoreA > user.bets[key].scoreB) ? "A" : "B";

            if (winTeam === winBet) {
                countLinkBetsValid += 1;
            }
            else {
                countLinkBetsFalse += 1;
            }
            switch (user.bets[key].type) {

                case 'win':
                    result.win += 1
                    break;
                case 'winTeam':
                    result.winTeam += 1
                    break;
                case 'lose':
                    result.lose += 1
                    break;
                case 'loseTeam':
                    result.loseTeam += 1
                    break;
                case 'full':
                    result.full += 1
                    break;
                case 'fullTeam':
                    result.fullTeam += 1
                    break;
                default:
                    break;
            }

            // console.log(linkBetsValid);
            // console.log(countLinkBetsValid);
            // console.log(linkBetsFalse);
            // console.log(countLinkBetsFalse);
        }
        return true;
    })

    if (countLinkBetsValid >= 1) {
        data = {
            valid: true,
            state: 1,
            icon: "bet-true.png",
            title: "Le dbut de la gloire",
            name: "bets-true-1",
            hide: false,
            messag: `${countLinkBetsValid}/5`
        }
        if (countLinkBetsValid >= 5) {
            data = {
                valid: true,
                state: 2,
                icon: "bet-true.png",
                title: "Un Pas de plus",
                name: "bets-true-2",
                hide: false,
                messag: `${countLinkBetsValid}/10`
            }
        }
        if (countLinkBetsValid >= 10) {
            data = {
                valid: true,
                state: 3,
                icon: "bet-true.png",
                title: "Le chemin est long",
                name: "bets-true-3",
                hide: false,
                messag: `${countLinkBetsValid}/25`
            }
        }
        if (countLinkBetsValid >= 25) {
            data = {
                valid: true,
                state: 4,
                icon: "bet-true.png",
                title: "Toujour tout droit",
                name: "bets-true-4",
                hide: false,
                messag: `${countLinkBetsValid}/50`
            }
        }
        if (countLinkBetsValid >= 50) {
            data = {
                valid: true,
                state: 5,
                icon: "bet-true.png",
                title: "C'est plus si loin",
                name: "bets-true-5",
                hide: false,
                messag: `${countLinkBetsValid}/100`
            }
        }
        Badges.push(<Badge data={data} user={user} key={data.name} />)
    }



    if (countLinkBetsFalse >= 1) {
        data = {
            valid: true,
            state: 1,
            icon: "bet-false.png",
            title: "Boulet de Bronze",
            name: "bets-false-1",
            hide: false,
            messag: `${countLinkBetsFalse}/5`
        }
        if (countLinkBetsFalse >= 5) {
            data = {
                valid: true,
                state: 2,
                icon: "bet-false.png",
                title: "Boulet d'Argent",
                name: "bets-false-2",
                hide: false,
                messag: `${countLinkBetsFalse}/10`
            }
        }
        if (countLinkBetsFalse >= 10) {
            data = {
                valid: true,
                state: 3,
                icon: "bet-false.png",
                title: "Boulet d'Or",
                name: "bets-false-3",
                hide: false,
                messag: `${countLinkBetsFalse}/25`
            }
        }
        if (countLinkBetsFalse >= 25) {
            data = {
                valid: true,
                state: 4,
                icon: "bet-false.png",
                title: "Boulet de Platine",
                name: "bets-false-4",
                hide: false,
                messag: `${countLinkBetsFalse}/50`
            }
        }
        Badges.push(<Badge data={data} user={user} key={data.name} />)
    }




    if (result.fullTeam >= 1) {
        data = {
            valid: true,
            state: 1,
            icon: "fullTeam.png",
            title: "T'es con, C'est mon \"team\" !!!",
            name: "fullTeam-1",
            hide: false,
            messag: `${result.fullTeam}/5`
        }
        if (result.fullTeam >= 5) {
            data = {
                valid: true,
                state: 2,
                icon: "fullTeam.png",
                title: "La confiance",
                name: "fullTeam-2",
                hide: false,
                messag: `${result.fullTeam}/10`
            }
        }
        if (result.fullTeam >= 10) {
            data = {
                valid: true,
                state: 3,
                icon: "fullTeam.png",
                title: "Le travaille d'quipe",
                name: "fullTeam-3",
                hide: false,
                messag: `${result.fullTeam}/25`
            }
        }
        if (result.fullTeam >= 25) {
            data = {
                valid: true,
                state: 4,
                icon: "fullTeam.png",
                title: "Good Like",
                name: "fullTeam-4",
                hide: false,
                messag: `${result.fullTeam}/50`
            }
        }
        if (result.fullTeam >= 50) {
            data = {
                valid: true,
                state: 5,
                icon: "fullTeam.png",
                title: "Good Like",
                name: "fullTeam-5",
                hide: false,
                messag: `${result.fullTeam}/100`
            }
        }
        Badges.push(<Badge data={data} user={user} key={data.name} />)
    }


    function shuffle(a) {
        // for (let i = a.length - 1; i > 0; i--) {
        //     const j = Math.floor(Math.random() * (i + 1));
        //     [a[i], a[j]] = [a[j], a[i]];
        // }
        return a;
    }


    return (
        <div style={{
            display: 'flex',
            flexWrap: 'wrap',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
            {shuffle(Badges)}
        </div>
    )
}




function Badge({ data, user }) {
    const [hover, setHover] = useState(false);
    if (data.valid === false && data.hide === true) {
        return '';
    } else {
        const parseMessage = data.messag.split('/');


        if (user.login) {
            if (user.achievement) {
                if (!user.achievement[data.name]) {
                    axios.post('../api/sendAchievement', {
                        achievement: data.name,
                        user: user.login,
                    }).catch(function (error) {
                        console.log(error);
                    });
                }

            }
        }


        return (
            <div style={{
                height: '50px',
                width: '50px',
                margin: '5px',
                border: '2px solid #cc682d',
                background: `#ffffff center/70% url("../imgs/hf/${data.icon}") no-repeat`,
                position: 'relative',
            }} onMouseEnter={({ currentTarget }) => {
                currentTarget.querySelector('.messag').style['display'] = 'block';
            }} key={data.id}>
                {(data.state) ?
                    <div style={{
                        position: 'absolute',
                        top: '0',
                        left: '0',
                        height: '30px',
                        width: '30px',
                        background: `center/100% url("../imgs/hf/T${data.state}.png") no-repeat`
                    }} />
                    : ''}
                {/* {data.icon} */}
                <div className="messag" onMouseLeave={({ currentTarget }) => {
                    currentTarget.style['display'] = 'none';
                }} style={{
                    position: 'absolute',
                    top: '0',
                    left: '-50%',
                    minHeight: '50px',
                    width: '200px',
                    border: '2px solid #cc682d',
                    background: `#ffffff`,
                    display: 'none',
                    zIndex: '1000',
                    padding: '10px 20px',
                }}>
                    <div style={{
                        fontSize: '1.4em',
                        textAlign: 'center',
                        marginBottom: '10px',
                    }}>
                        {data.title}
                    </div>
                    {(parseMessage.length > 1) ?
                        <div style={{
                            textAlign: 'center',
                        }}>
                            <progress id="file" max={parseMessage[1]} value={parseMessage[0]}> {parseMessage[0]}</progress>
                        </div>


                        : false}


                    <div style={{
                        fontSize: '1.2em',
                        textAlign: 'center',
                        marginTop: '20px',
                    }}>
                        {data.messag}
                    </div>
                </div>
            </div>
        )
    }

}

export default Achievement;